@use '../helpers/abstracts' as *;

.production_content {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	gap: 18rem;
}

.production_content_col_1 {
	width: 60%;
	position: relative;
}

.production_content_col_2 {
	position: relative;
}

.section_title__production {
	margin-bottom: 2.6rem;
	padding-left: 17.3rem;
}

.section_title__production::before {
	position: absolute;
	content: '';
	top: 3rem;
	left: 0rem;
	width: 12rem;
	height: 0.4rem;
	background-color: $color_accent;
	margin-right: 4.3rem;
}

.production_descr {
	font-family: $font_1;
	font-size: 2rem;
	font-weight: 400;
	line-height: 3rem;
	padding-left: 17.3rem;
	color: $color_descr_1;
}

.picture_w {
	width: 46rem;
	height: 46rem;
}

.picture_w::before {
	content: '';
	position: absolute;
	top: 1%;
	left: 100%;
	transform: translate(-50%, -50%);
	width: 9rem;
	height: 9rem;
	background-color: $color_accent;
}

.production_content_col_2::before {
	content: '';
	position: absolute;
	top: 24%;
	left: -13%;
	width: 29rem;
	height: 38rem;
	background: url(../imgs/production/decor_production.svg) no-repeat center/contain;
}

.production__img {
	position: relative;
	width: 100%;
	height: 100%;
	object-fit: cover;
	z-index: 1;
}
